.offer__slider-container {
  padding: 0 25px;
  background: rgba(0, 61, 166, 0.9);
  position: relative;


  @include landscape {
    width: 30%;
    padding: 0 30px;
   
  }

  @include desktop {
    min-width: 23%;
  }

  @include desktop {
    min-width: 25%;
  }
   @include desktop-xhd {
    padding-top: 6%;  
  }
}

.slider {

  .slide {
    @extend %flex-column;

    @include portrait {
      flex-direction: row;
    }

    @include landscape {
      flex-direction: column;
    }

    &__image {

      filter: grayscale(100);

      @include portrait {
        flex: 1;
        padding-bottom: 65px;

        img {
          height: auto;
        }
      }

      @include landscape {
        display: flex;
        flex-direction: column;
        padding-bottom: 0;
        padding-top: 90px;
      }

      @include desktop-hd {
        flex: 1;
        padding-bottom: 30px;

        img {
          height: auto;
        }
      }

      .slide-counter {
        padding: 50px 0 30px 0;
        @extend %slider-text;
        letter-spacing: 1px;
        font-size: 18px;

        @include landscape {
          order: 2;
          text-transform: uppercase;
          padding-bottom: 0;
        }

        @include desktop {
          font-size: 21px;
        }
      }
    }

    &__description {
      display: flex;

          @include portrait{
            padding-top: 45px;  
              }

              @include landscape{
                 padding-top: 0px;  
                    }
      p {
        @extend %slider-text;
        font-size: 12px;
        padding: 30px 0 70px 0;

        @include portrait {
          flex: 1;
          padding-left: 25px;
          padding-top: 60px;
          display: flex;
          align-items: center;
          font-size: 14px;
        }

        @include landscape {
          padding: 25px 0 0 0;
        }

        @include desktop-hd {
          padding:0;
          font-size: 16px;
          line-height: 1.3;
        }

      }
    }
  }



    @include portrait {
      bottom: 20px;
    }

    @include landscape {
      bottom: 141px;
    }

    @include desktop-small-height {
      bottom: 30px;
    }





  

.slick-dots{
  display: flex;  
  justify-content: center;  
  bottom: 0; 

   @include landscape {
      bottom: -90px;
    }

  li{


    width: 12px;  
    height: 12px; 
    border: 1px solid #fff;
    border-radius: 100%;
    @extend %flex-total-center;  

           &.slick-active{
       button{
        width: 6px; 
      height: 6px;  
      background: #fff;
      border-radius: 100%;
       }
    }
  }
}

}


  .slick-initialized .slick-slide{
     display: flex !important;
     align-items: center; 
    }
