.header {
  .header-col-left__language {
    display: flex;
    padding-left: 10px;


    @include portrait {
      padding-left: 20px;
    }

    .lang-switch {
      font-size: 28px;
      color: $light-dark;
      font-family: $primary-font-medium;
      letter-spacing: 2px;

      &.active {
        font-family: $primary-font-bold;
      }
    }
  }

}


.header__content-page {
  .header-col-left__language {
    padding-left: 0px;

    .lang-switch {
      padding-right: 10px;
    }
  }
}