.header,
.footer {
  background: $light-color;
  position: relative;
  display: flex;
  align-items: center;
}

.header {
  position: fixed;
  width: 100%;
  z-index: 999;
}

.container {
  @include landscape {
    max-width: 960px;
  }

  @include desktop {
    max-width: 1140px;
  }

  max-width: 100%;
  margin: 0 auto;
}

.row {
  display: flex;
  width: 100%;
}

.col {
  flex: 1;
}

.col-2 {
  @include portrait {
    flex: 2;
  }
}

.col-3 {
  @include landscape {
    flex: 3;
  }

  @include desktop-xhd {
    flex: 3;
  }
}

.col-4 {
  @include desktop-xhd {
    flex: 4;
  }
}

.footer__main-page,
.footer__content-page {
  padding: 20px 28px 20px 28px;
  justify-content: space-between;
  align-items: center;
  width: 100%;

  @include desktop {
    padding: 20px 30px;
  }

  @include desktop-hd {
    padding: 35px 150px 35px 150px;
  }
}

.row-col {
  flex-direction: column;
}

.header__title-home {
  @extend %project-title;
}

.galery__project-row-col,
.projects__row-col {
  @extend %flex-column;

  @include portrait {
    display: flex;
    flex-direction: row;
  }
}


.galery__project-row-col {
  @include portrait {
    flex-wrap: wrap;
  }
}