.offer__content-text__title {
  h2 {
    margin: 0;
    padding: 30px 0 16px 0;
    font-size: 28px;
    line-height: 1.1;

    @include portrait {
      font-size: 54px;
      padding-bottom: 35px;
    }

    @include landscape {
      padding-top: 60px;
      font-size: 55px;
    }

    @include desktop {
      font-size: 72px;
      padding-bottom: 17px;
    }

    @include desktop-hd {
      padding-right: 60px;
    }
  }

  @extend %same-color-font;

}