html {
	overflow-x: hidden;
}

body {
	margin: 0;
	padding: 0;
	background: $light-color;
	font-family: $primary-font, $secondary-font, $meta-font, sans-serif;
	font-size: 100%;
	color: $light-color;
	overflow-x: hidden;
}


img {
	width: 100%;
	height: 100%;
}

a {
	text-decoration: none;
	cursor: pointer;
}

button {
	cursor: pointer;
	outline: none;
	border: none;
	background: transparent;
}

ul {
	list-style: none;
	padding: 0;
	margin: 0;
}