.header-col-left {
  .header-col-left__hamburger-btn {
    background: transparent;
    outline: none;
    position: relative;
    border: none;
    box-shadow: none;
    box-sizing: border-box;
    cursor: pointer;
    width: 30px;
    height: 30px;

    .hamburger-inner {
      @extend %hamburger-inner;
      top: 5px;
      transform-origin: center;

      &:before,
      &:after {
        content: "";
        @extend %hamburger-inner;
        display: inline-block;
      }

      &:before {
        top: 10px;
      }

      &:after {
        top: 20px;
      }

      &.active {
        transform: rotate(45deg);
        top: 15px;

        &:before {
          content: "";
          @extend %hamburger-inner;
          display: inline-block;
          transform: rotate(90deg);
          top: 0;
        }

        &:after {
          display: none;
        }
      }
    }
  }
}