.projects__btn {
  background: $overlay-color;
  padding: 10px 10px 18px 10px;

  &-header {
    position: relative;
    display: flex;
    align-items: center;

    @include landscape {
      align-items: center;
    }

    .projects__btn-header-arrow {
      img {
        width: 39px;
        height: 30px;
      }

      @include desktop {
        width: 50px;
      }

      font-size: 30px;
    }

    img {
      width: auto;
      height: auto;
    }
  }

  &-header-text {
    font-size: 18px;
    color: $light-color;
    padding-left: 15px;
    padding-bottom: 10px;

    @include desktop {
      font-size: 24px;
    }
  }

  &-text {
    p {

      font-size: 14px;
      padding: 15px 0 10px 0;
      color: $light-color;

      @include portrait {
        padding-bottom: 0;
        padding-top: 10px;
      }

      @include landscape {
        padding-left: 55px;
        padding-right: 0;
      }

      @include desktop {
        padding-left: 67px;
        padding-right: 0;
        padding-top: 0px;
        font-size: 18px;
      }
    }
  }
}