.navmenu {
  &__wrapper {
    position: fixed;
    background: #fff;
    width: 100%;
    height: 100vh;
    z-index: 20;
    left: -500%;
    transition: all 0.3s;

    &.active {
      left: 0;
      top: 50px;

      @include desktop-small-height {
        overflow-y: scroll;
      }

      @include mobile-landscape {
        width: 60%;
        padding-top: 53px;
      }


      @include portrait {
        width: 40%;
        padding-top: 0px;
      }

      @include landscape {
        width: 35%;
      }

      @include desktop {
        width: 25%;
      }
    }

    @extend %flex-column;
    padding: 30px 25px;

    h2 {
      @include mobile {
        width: 100%;
      }

      @include landscape {
        padding-top: 10px;
        font-size: 27px;
      }

      @include desktop {
        font-size: 29px;
      }


    }
  }

  @include portrait {
    top: 0;
  }

  &__title {
    h2 {
      font-family: $primary-font-medium;
      color: $primary-color;
    }
  }

  &__subtitle {

    padding-right: 50px;

    @include portrait {
      padding-right: 0;
    }

    p {
      font-family: $primary-font-medium;
      color: $dark-grey;
      padding-top: 15px;
      font-size: 18px;
      line-height: 1.5;

      @include desktop {
        font-size: 20px;
      }
    }
  }


  &__accordion {

    &-btn {
      padding: 30px 0;
      font-family: $primary-font-medium;
      font-size: 24px;
      border: none;
      color: $primary-color;
      position: relative;

      &:before,
      &:after {
        content: "";
        position: absolute;
        right: -25px;
        top: 43px;
        width: 14px;
        height: 3px;
        transition: all 0.3s;
        background: $dark-grey;
      }

      &:before {}

      &:after {
        transform: rotate(270deg);
      }

      &.is_active:after {
        transform: rotate(0deg);
      }

      @include landscape {
        font-size: 27px;
      }

      @include desktop {
        font-size: 29px;
      }
    }
  }

  .nav {
    font-family: $primary-font-medium;
    padding-left: 20px;
    position: relative;
    overflow: hidden;

    &-list {
      transform: translateY(-200%);
      transition: transform 0.3s;
      height: 0;

      &.is_active {
        transform: translateY(0);
        height: auto;
      }
    }

    &-item {
      padding: 10px 0;
      position: relative;
    }

    &-link {
      color: $dark-grey;
      padding-left: 20px;
      transition: all 0.3s;

      &:hover {
        color: $green-light-color;
      }

      &:before {
        content: "";
        width: 3px;
        height: 20px;
        background: $dark-grey;
        position: absolute;
        top: 50%;
        left: 0;
        transform: rotate(-45deg) translateY(-50%);
      }

      &:hover:before {
        transform: rotate(45deg) translateY(50%);
        top: 0;
      }
    }
  }

}