.offer__content {
  position: relative;
  min-height: 768px;
  max-height: 960px;
  background: url("../images/homepage/forest.png");
  background-repeat: no-repeat;
  background-size: cover;
  @extend %flex-column;
  justify-content: space-between;

  @include landscape {
    flex-direction: row;
  }

  &-text {
    padding: 35px 25px;

    @include portrait {
      padding-right: 100px;
    }

    @include desktop-hd {
      padding-left: 150px;
    }
  }
}



.content-page__offer-content {
  position: relative;

  .top-img-small {
    @include landscape {
      display: none;
    }
  }

  .top-img-big {
    display: none;

    @include landscape {
      display: block;
    }
  }

  .bottom-background {
    bottom: -10px;
    left: 0;
    right: 0;
    width: 131%;
    display: block;

    @include landscape {
      width: 132%;
      bottom: -71px;
      left: -19%
    }
  }


}


.bottom-background {
  position: absolute;
  bottom: -10px;
  left: 0;
  right: 0;
  display: none;

  @include landscape {
    display: block;
  }
}