.footer {
  background: $light-grey;

  &__copyright {

    &-text {
      color: $primary-color;
      font-family: $primary-font-medium;
      font-size: 17px;

      span {
        font-family: $helvetica;
        font-size: 15px;

        @include desktop {
          font-size: 16px;
        }
      }

      @include desktop {
        font-size: 18px;
      }

    }
  }
}