@font-face {
  font-family: $helvetica;
  font-style: 400;
  font-weight: 400;
  src: url("../fonts/helvetica.ttf");
}

@font-face {
  font-family: $primary-font;
  font-style: 400;
  font-weight: 400;
  src: url("../fonts/Rajdhani/rajdhani.woff2");
}

@font-face {
  font-family: $primary-font-medium;
  font-style: 500;
  font-weight: 500;
  src: url("../fonts/Rajdhani/rajdhani-medium.woff2");
}

@font-face {
  font-family: $primary-font-bold;
  font-style: 700;
  font-weight: 700;
  src: url("../fonts/Rajdhani/rajdhani-bold.woff2");
}

@font-face {
  font-family: $secondary-font;
  font-style: 400;
  font-weight: 400;
  src: url("../fonts/Arvo/Arvo.woff2");
}

@font-face {
  font-family: $meta-font;
  font-style: 200;
  font-weight: 200;
  src: url("../fonts/font4.woff2");
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  margin: 0;
  padding: 0;
}

h2 {
  font-size: 24px;
}