.header {

	&__main-page,
	&__content-page {
		justify-content: space-between;
		padding: 10px 28px 0px 28px;

		@include desktop {
			padding: 10px 30px 0px 30px;
		}
	}

	&__main-page,
	&__content-page {
		transition: padding 0.5s;

		&.active {
			@include portrait {
				padding-left: (48%);
			}

			@include landscape {
				padding-left: (40%);
			}

			@include desktop {
				padding-left: (30%);
			}
		}
	}

	.header-col-left,
	.header-col-right {
		display: flex;
		align-items: center;
	}

	.header-col-right {
		&__logo-dreso {
			padding: 0 0 0 20px;

			@include portrait {
				padding-left: 30px !important;
			}
		}
	}
}