%hamburger-inner {
  width: 24px;
  height: 2px;
  background: $light-dark;
  position: absolute;
  left: 0;
}

%fullsize {
  height: 100%;
  width: 100%;
}

%flex-column {
  display: flex;
  flex-direction: column;
}

%flex-total-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

%flex-vertical-center {
  display: flex;
  align-items: center;
}

%flex-row {
  display: flex;
  flex-direction: row;
}


%same-color-font {
  color: $light-color;
  font-family: $primary-font;
  letter-spacing: 1px;
}

%slider-text {
  font-family: $meta-font;
  color: $light-color;
}

%primary-color-font {
  font-family: $primary-font;
  color: $primary-color;
}

%project-title {
  font-family: $primary-font-medium;
  color: $primary-color;
  font-size: 19px;
  line-height: 1.1;
}

%project-text {
  font-family: $primary-font-medium;
  color: $primary-color;
  font-size: 15px;
  line-height: 1.5;
}