  .projects__about,
  .galery__project {
    p {

      @extend %project-text;
      padding-bottom: 25px;
    }
  }

  .projects {
    padding: 75px 30px 0px 30px;

    @include desktop {
      padding: 30px 100px 0px 100px;
    }

  }

  .galery__project {
    padding: 75px 20px 0px 20px;

    @include portrait {
      padding-top: 30px;
    }

    .galery__project-container {
      @include desktop {
        padding: 30px 100px 0px 100px;
      }

    }

    @include landscape {
      padding: 30px 20px 0px 20px;
    }


  }

  .header__title-home {

    @include portrait {
      text-align: left;
      width: 82%;
      padding-bottom: 0px;
    }

    h2 {
      @include portrait {
        font-size: 29px;
      }

      @include landscape {
        font-size: 35px;
      }

    }


  }

  .projects__about {

    @include portrait {
      flex: 3;

      p {
        padding-right: 10px;
        font-size: 17px;
        line-height: 1.4;

        @include desktop {
          margin-right: 40px;
          font-size: 20px;
          line-height: 1.5;

        }

      }
    }

  }

  .projects__row-col {
    margin-top: 30px;
  }



  .circle-progress {
    @extend %flex-total-center;
    padding-bottom: 30px;

    @include desktop-hd {
      padding-bottom: 90px;
    }
  }

  .galery__project-text {
    display: flex;
    height: 100%;


    @include desktop {
      align-items: flex-start;
    }

    p {
      padding: 30px 8px 0 8px;

      @include landscape {
        line-height: 1.5;
        font-size: 18px;
      }

      @include desktop {
        padding-top: 15px;
        padding-right: 60px;
        font-size: 20px;
        line-height: 1.5;
      }

    }

  }

  .galery__project-card {
    padding: 0 5px;

    &::first-child {
      padding-left: 0;
    }

    &::last-child {
      padding-right: 0;
    }
  }

  .galery__project-block {
    padding: 35px 0;
    display: flex;
    justify-content: center;
    position: relative;

    @include landscape {
      justify-content: flex-start;
    }

    @include desktop-hd {
      padding: 0 128px;
    }

    .galery__project-box {
      max-width: 100%;
      height: 160px;
      padding-bottom: 15px;
      position: relative;

      @include portrait {
        width: 230px;
        height: 140px;
      }

      @include desktop {
        width: 285px;
        height: 175px;
      }

      @include desktop-xhd {
        min-width: 340px;
        min-height: 185px;
      }



      img {
        filter: grayscale(100);
      }

      .galery__project-number {
        position: absolute;
        top: 5px;
        left: 5px;
        width: 40px;
        height: 40px;
        background: $light-color;
        border-radius: 100%;
        display: flex;
        justify-content: center;
        font-size: $font-medium;
        color: $green-light-color;
        font-family: $helvetica;

        @include desktop {

          width: 50px;
          height: 50px;
          top: 10px;
          left: 10px;
        }
      }

      .galery__project-name {
        position: absolute;
        bottom: 15px;
        left: 0;
        padding: 18px;
        @extend %flex-column;

        span {
          background: $overlay-color;
          color: $light-color;
          font-size: $font-small;
          padding: 3px 10px 3px 3px;
          margin-top: 5px;

          @include desktop {
            font-size: 18px;
          }
        }


      }
    }
  }